import React, { PureComponent } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import Title from './Title';

const data = [
  {
    name: 'BHU Fakir Kalay',
    ANC_Old: 4,
    ANC_New: 6,
    amt: 15,
  },
  {
    name: 'BHU Kohat',
    ANC_Old: 3,
    ANC_New: 13,
    amt: 15,
  },
  {
    name: 'BHU Warsak Road',
    ANC_Old: 2,
    ANC_New: 9,
    amt: 15,
  },
  {
    name: 'BHU Regi',
    ANC_Old: 7,
    ANC_New: 9,
    amt: 15,
  },
  {
    name: 'BHU Palosai',
    ANC_Old: 18,
    ANC_New: 8,
    amt: 15,
  },
  {
    name: 'BHU Nahaqi',
    ANC_Old: 9,
    ANC_New: 8,
    amt: 15,
  },
  {
    name: 'BHU Andar Sher',
    ANC_Old: 4,
    ANC_New: 4,
    amt: 15,
  },
];

export default class Example extends PureComponent {
  static demoUrl = 'https://codesandbox.io/s/simple-bar-chart-tpz8r';

  render() {
    return (
        <>
        <Title>Number of ANC (New and Old) HF-Wise</Title>
      <ResponsiveContainer width="100%" height="100%">
          
        <BarChart
          width={500}
          height={300}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Bar dataKey="ANC_New" fill="#8884d8" />
          <Bar dataKey="ANC_Old" fill="#82ca9d" />
        </BarChart>
      </ResponsiveContainer>
      </>
    );
  }
}