import * as React from 'react';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import DashboardIcon from '@mui/icons-material/Dashboard';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import BarChartIcon from '@mui/icons-material/BarChart';
import LayersIcon from '@mui/icons-material/Layers';
import AssignmentIcon from '@mui/icons-material/Assignment';
import PhoneCallbackIcon from '@mui/icons-material/PhoneCallback';
import {Link} from 'react-router-dom'
import './listItems.css'
import BabyChangingStationIcon from '@mui/icons-material/BabyChangingStation';
import HomeIcon from '@mui/icons-material/Home';


export const mainListItems = (
  <div>
    <ListItem button>
      <ListItemIcon>
        <Link to ="/dashboard/" className="Dashhome"><DashboardIcon /></Link></ListItemIcon>
      <Link to ="/dashboard/" className="Dashhome">Home</Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/checkinsms" className="Checkin"><PhoneCallbackIcon /></Link>
      </ListItemIcon>
      <Link to ="/dashboard/checkinsms" className="Checkin">Check in SMS</Link>
    </ListItem>
       <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/checkoutsms" className="Checkout"><PhoneInTalkIcon /></Link>
      </ListItemIcon>
      <Link to ="/dashboard/checkoutsms" className="Checkout">Check out SMS</Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/dsr" className="Dsr"><BarChartIcon /></Link> 
      </ListItemIcon>
      <Link to ="/dashboard/dsr" className="Dsr">Daily Status SMS</Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms"><LayersIcon /></Link>
      </ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms">IMU Verifications</Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms"><BabyChangingStationIcon /></Link>
      </ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms">SBA List</Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms"><HomeIcon /></Link> 
      </ListItemIcon>
      <Link to ="/dashboard/imu-vsms" className="IMU-Vsms">Health facility List</Link>
    </ListItem>
  </div>
);

export const secondaryListItems = (
  <div>
    <ListSubheader inset>Saved reports</ListSubheader>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Current month" />
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Last quarter" />
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Year-end sale" />
    </ListItem>
  </div>
);