import * as React from 'react';
import Link from '@mui/material/Link';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Title from './Title';
import TextField from '@mui/material/TextField';

// Generate Order Data
function createData(id, date, name, shipTo, paymentMethod, paymentMethod2, paymentMethod3, paymentMethod4, paymentMethod5, paymentMethod6, paymentMethod7) {
  return { id, date, name, shipTo, paymentMethod, paymentMethod2, paymentMethod3, paymentMethod4, paymentMethod5, paymentMethod6, paymentMethod7 };
}

const rows = [
  createData(
    0,
    '20',
    '2236',
    'Yes',
    'No',
    'No',
    'Kamran Khan',
    '20-9-2021',
    '15:30',
    'BHU Fakir Kalay',
     
  ),
  createData(
    1,
    '21',
    '2237',
    'Yes',
    'No',
    'No',
    'Kamran Khan',
    '20-9-2021',
    '16:00',
    'BHU Kohat Road',
  ),
  createData(
    2, 
    '21',
    '2237',
    'Yes',
    'No',
    'No',
    'Zain Afridi',
    '20-9-2021',
    '15:30',
    'RHC Regi',
    ),
  createData(
    3,
    '22',
    '2237',
    'No',
    'Yes',
    'Yes',
    'Nisar Khan',
    '20-9-2021',
    '13:00',
    'RHC Potwar',
  ),
  createData(
    4,
    '23',
    '2238',
    'Yes',
    'No',
    'No',
    'Waqas Khan',
    '20-9-2021',
    '15:30',
    'BHU Regi',
  ),
];

function preventDefault(event) {
  event.preventDefault();
}

export default function Orders() {
  return (
    <React.Fragment>
      <Title>IMU Verification</Title>
      <TextField id="outlined-basic" label="Search IMU Verifications..." variant="outlined" />
      <Table size="medium">
        <TableHead>
          <TableRow>
          
           
            <TableCell>IMU-VSMS ID</TableCell>
            <TableCell>Case ID</TableCell>
            <TableCell>Delivery Verified</TableCell>
            <TableCell>Delivery Not Verified</TableCell>
            <TableCell>Patient Not Available</TableCell>
            <TableCell>IMU DEA Name</TableCell>
            <TableCell>Date of Verification</TableCell>
            <TableCell>Time of Verification</TableCell>
            <TableCell>Health Facility Name</TableCell>
           
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.date}</TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell>{row.shipTo}</TableCell>
              <TableCell>{row.paymentMethod}</TableCell>
              <TableCell>{row.paymentMethod2}</TableCell>
              <TableCell>{row.paymentMethod3}</TableCell>
              <TableCell>{row.paymentMethod4}</TableCell>
              <TableCell>{row.paymentMethod5}</TableCell>
              <TableCell>{row.paymentMethod6}</TableCell>
              <TableCell>{row.paymentMethod7}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Link color="primary" href="#" onClick={preventDefault} sx={{ mt: 3 }}>
        See more
      </Link>
    </React.Fragment>
  );
}