import React, { PureComponent } from 'react';
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ReferenceLine,
  ResponsiveContainer,
} from 'recharts';
import Title from './Title';

const data = [
  {
    name: 'Peshawar',
    Female: 4000,
    Male: 2400,
    amt: 2400,
  },
  {
    name: 'Mardan',
    Female: 3000,
    Male: 1398,
    amt: 2210,
  },
  {
    name: 'Karak',
    Female: 2000,
    Male: 9800,
    amt: 2290,
  },
  {
    name: 'Swabi',
    Female: 2780,
    Male: 3908,
    amt: 2000,
  },
  {
    name: 'Dir Upper',
    Female: 1890,
    Male: 4800,
    amt: 2181,
  },
  {
    name: 'Charsadda',
    Female: 2390,
    Male: 3800,
    amt: 2500,
  },
  {
    name: 'Bannu',
    Female: 3490,
    Male: 4300,
    amt: 2100,
  },
];

export default class Example extends PureComponent {
  static demoUrl = 'https://codesandbox.io/s/bar-chart-with-positive-negative-i3b8b';

  render() {
    return (
        <>
        <Title>Gender of Deliveries (District wise)</Title>
      <ResponsiveContainer width="100%" height="100%">
        <BarChart
          width={500}
          height={300}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          <ReferenceLine y={0} stroke="#000" />
          <Bar dataKey="Male" fill="#8884d8" />
          <Bar dataKey="Female" fill="#82ca9d" />
        </BarChart>
      </ResponsiveContainer>
      </>
    );
  }
}