import React from 'react';
import { Pie } from 'react-chartjs-2';
import Typography from '@mui/material/Typography';

const data = {
  labels: ['Peshawar', 'Kohat', 'Mardan', 'Charsadda', 'Nowshera', 'FR Peshawar'],
  datasets: [
    {
      label: '# of Votes',
      data: [12, 19, 3, 5, 2, 3],
      backgroundColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(153, 102, 255, 1)',
        'rgba(255, 159, 64, 1)',
      ],
      borderColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(153, 102, 255, 1)',
        'rgba(255, 159, 64, 1)',
      ],
      borderWidth: 1,
    },
  ],
};

const LiveChart = () => (
  <>
    <div className='header'>
    <Typography component="h2" variant="h6" color="primary" gutterBottom>
    Number of Deliveries Verified District Wise 
    (20th September 2021)
    </Typography>
      <h1 className='title'></h1>
    </div>
    <Pie data={data} />
  </>
);

export default LiveChart;