import './App.css';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import SignInSide from './SignInSide'
import Dashboard from './Dashboard'
import IMUVerifications from './IMUVerifications';
import CheckinSMS from './CheckinSMS';


function App() {
  return (
<Router>
  <Switch>
<div className="App">

  <Route exact path ="/signin" component ={SignInSide} />
  <Route exact path ="/" component ={Dashboard} />
  <Route exact path ="/dashboard/imu-vsms" component ={IMUVerifications} />
  <Route exact path ="/Check-in-sms" component ={CheckinSMS} />
  
    </div>
    </Switch>
    </Router>
  );
}

export default App;
