import React, { PureComponent } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import Title from './Title';

const data = [
  {
    name: 'Peshawar',
    BHU_Fakir_Kalay: 4,
    BHU_Warsak_Road: 6,
    BHU_Kohat_Road: 6,
    BHU_Palosai: 6,
    BHU_Potwar: 6,
    BHU_Dalazak: 6,
    BHU_Pishtakhara: 6,
    BHU_Chamkani: 6,
    amt: 15,
  },
  {
    name: 'BHU Kohat',
    PNC_Old: 3,
    PNC_New: 13,
    amt: 15,
  },
  {
    name: 'BHU Warsak Road',
    PNC_Old: 2,
    PNC_New: 9,
    amt: 15,
  },
  {
    name: 'BHU Regi',
    PNC_Old: 7,
    PNC_New: 9,
    amt: 15,
  },
  {
    name: 'BHU Palosai',
    PNC_Old: 18,
    PNC_New: 8,
    amt: 15,
  },
  {
    name: 'BHU Nahaqi',
    PNC_Old: 9,
    PNC_New: 8,
    amt: 15,
  },
  {
    name: 'BHU Andar Sher',
    PNC_Old: 4,
    PNC_New: 4,
    amt: 15,
  },
];

export default class Example extends PureComponent {
  static demoUrl = 'https://codesandbox.io/s/simple-bar-chart-tpz8r';

  render() {
    return (
        <>
        <Title>Number of PNC (New and Old) HF-Wise</Title>
      <ResponsiveContainer width="100%" height="100%">
          
        <BarChart
          width={500}
          height={300}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Bar dataKey="PNC_New" fill="#a83232" />
          <Bar dataKey="PNC_Old" fill="#13166b" />
        </BarChart>
      </ResponsiveContainer>
      </>
    );
  }
}